'use client'

import { Quantico } from 'next/font/google'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Card } from '@/components/ui/card'

const quantico = Quantico({
    weight: ['400', '700'],
    subsets: ['latin'],
    display: 'swap',
    variable: '--font-quantico',
})

type RecordItem = {
    pos: number
    driver: string
    time: string
    laps: number
    kart: string
}

const records: RecordItem[] = [
    { pos: 1, driver: "Wan", time: "00:57.22", laps: 5, kart: "S5" },
    { pos: 2, driver: "Irfan", time: "00:58.10", laps: 5, kart: "S4" },
    { pos: 3, driver: "Naim", time: "00:59.88", laps: 5, kart: "S3" },
]

export default function Page() {
    return (
        <div className="flex flex-col p-15 space-y-6">
            <div className="w-full">
                <h2 className={`text-5xl text-white text-shadow-accent-foreground text-center font-bold ${quantico.className}`}>
                    TRACK RECORDS
                </h2>
            </div>

            <div className="flex flex-row justify-center bg-transparent border-transparent">
                <Card className="p-4 bg-[#6b0000] border border-red-800/40 shadow-xl rounded-2xl">
                    <Table className="border-separate border-spacing-y-3">
                        <TableHeader>
                            <TableRow className="bg-yellow-500 text-black rounded-xl hover:bg-yellow-500">
                                <TableHead className="font-bold text-center">POS</TableHead>
                                <TableHead className="font-bold text-center">DRIVER</TableHead>
                                <TableHead className="font-bold text-center">TIME</TableHead>
                                <TableHead className="font-bold text-center">LAP COMPLETED</TableHead>
                                <TableHead className="font-bold text-center">KART TYPE</TableHead>
                            </TableRow>
                        </TableHeader>

                        <TableBody className='rounded bg-accent '>
                            {records.map(item => (
                                <TableRow key={item.pos}>
                                    <TableCell>{item.pos}</TableCell>
                                    <TableCell>{item.driver}</TableCell>
                                    <TableCell>{item.time}</TableCell>
                                    <TableCell>{item.laps}</TableCell>
                                    <TableCell>{item.kart}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </Card>

            </div>
        </div>
    )
}
